@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 600;
}

:root {
  --main-color: #3d4e61;
}

html {
  color: var(--main-color);
  font-family: Avenir, sans-serif;
  font-weight: 300;
}

body {
  box-sizing: border-box;
}

.search {
  box-sizing: border-box;
  justify-content: center;
  margin: 20px auto 0;
  display: flex;
}

.search--big {
  width: 100%;
  height: 70px;
}

.search--small {
  width: 100%;
  height: 42px;
}

.search__label {
  width: 100%;
  display: block;
  position: relative;
}

.search__label:after {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
  position: absolute;
}

.search__label--big:after {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.search__label--small:after {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.search__input {
  box-sizing: border-box;
  color: var(--main-color);
  background-color: #fff;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  outline: none;
  width: 100%;
  height: 100%;
  font-family: inherit;
  font-weight: 300;
  transition: all .3s;
  box-shadow: 0 1px 8px #3d4e611a;
}

.search__input--big {
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

.search__input--small {
  padding-left: 33px;
  font-size: 14px;
  line-height: 19px;
}

.search__input::placeholder {
  color: var(--main-color);
  font-family: inherit;
  font-weight: 300;
}

.search__input--big::placeholder {
  font-size: 16px;
  line-height: 22px;
}

.search__input--small::placeholder {
  font-size: 14px;
  line-height: 22px;
}

.search__input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

.search__input:focus {
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 600;
}
